<template>
    <div class="container back-one">
        
        <div class="payment-confirm">
            <div class="one">
                <span>充值金额</span>
                <input type="text" :value="money" readonly>
                <span class="c">元</span>
            </div>
            <div class="two">最低限额：<span>{{select.minMoney}}</span>元，最高限额：<span>{{select.maxMoney}}</span>元</div>
            <div class="three">
                <span>转账信息</span>
                <input type="text" v-model="userYsfName" placeholder="请输入转出账户真实姓名">
            </div>
            <div class="btn">
                <button @click="submitFun">提交订单,审核入款!</button>
            </div>
        </div>

        <!-- 教程弹窗 -->
        <div class="alert-course" v-if="show2">
            <div class="layout">
                <h2>付款教程<var @click="show2=false">关闭</var></h2>
                <div class="box">
                    <div class="scroll">
                        <p><i>1、</i>打开微信，右上角+，点击扫一扫二维码进行转账</p>
                        <img src="../../static/img/wx/wxyhk_1.png" alt="">
                        <p><i>2、</i>添加收款人微信号（如：田伟伟），进行微信转账</p>
                        <img src="../../static/img/wx/wxyhk_2.png" alt="">
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                show2: false,


                skInfoList: false,

                userYsfName:'',
            }
        },
        created(){
            
        },
        computed:{
            ...mapState('Recharge', {
                user: state => state.user,
                select: state => state.select,
                money: state => state.money
            }),
            columns(){
                var arr = []
                if(this.skInfoList){
                    this.skInfoList.forEach(item=>{
                        arr.push(`${item.account}-${item.username}`)
                    })
                }
                return arr
            }
        },
        methods: {
            returnBack(){
                window.webkit.messageHandlers.back.postMessage("1");
            },
            submitFun(){
                if(this.userYsfName == ''){
                    this.$toast('请输入转出账户真实姓名');
                    return
                }
                var data = {
                    ...this.user,
                    userYsfName: this.userYsfName,
                    ysfId: this.select.id,
                    money: this.money
                }
                this.$api.recharge.submitYsfzz(data)
                .then(res=>{
                    if(res.result === 1){
                        this.$toast({message:`已成功提交\r\n等待3-10分钟查看余额`, duration: 3000,})
                    }else{
                        this.$toast(res.description);
                    }
                    setTimeout(()=>{
                        this.$router.back(-1);
                    }, 2000)
                })
            },
        },
        
    }
</script>

